/* nav list */
// nav opener btn
a.nav-opener {
	$btn-menu-bg: white;
	$btn-menu-color: $text-color;
	$btn-menu-size: 40px;
	$btn-menu-lines-width: 30px;
	$btn-menu-lines-height: 3px;
	$btn-menu-lines-space: 5px;
	$btn-menu-lines-color: $text-color;
	$btn-menu-animation-speed: $animation-speed;
	$btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

	$user-options-cross-width: $btn-menu-lines-width;
	$user-options-cross-height: $btn-menu-lines-height;
	$user-options-cross-color: $btn-menu-lines-color;

	// =====================

	display: none;
	position: fixed;
	right: 14px;
	top: round($header-height--sm/2);
	margin-top: -#{round($btn-menu-size/2)};
	z-index: 20;
	@include size($btn-menu-size);
	text-decoration: none;
	color: $text-color;
	background-color: $btn-menu-bg;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	@include media('<tab-lg') {
		display: block; }

	.touchevents & {
		// убрать подсветку при клике на тачскринах
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	& > span {
		@include hide-text;
		white-space: normal; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include size($user-options-cross-width, $user-options-cross-height);
		background-color: $user-options-cross-color;
		@include position(50%);
		@include margin($top: -#{round($user-options-cross-height*0.5)}, $left: -#{round($user-options-cross-width*0.5)});
		will-change: opacity, transform;
		opacity: 0;
		transition: {
			property: transform, opacity;
			duration: $btn-menu-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// бутерброд
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($btn-menu-lines-width, $btn-menu-lines-height);
		background-color: $btn-menu-lines-color;
		@include margin($top: -#{round($btn-menu-lines-height*0.5)}, $left: -#{round($btn-menu-lines-width*0.5)});
		backface-visibility: hidden;
		transition: {
			property: opacity;
			duration: $btn-menu-animation-speed;
			delay: $btn-menu-animation-delay; }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include size($btn-menu-lines-width, $btn-menu-lines-height);
			background-color: $btn-menu-lines-color;
			backface-visibility: hidden;
			transition: {
				property: transform;
				duration: $btn-menu-animation-speed;
				delay: $btn-menu-animation-delay; } }

		//first line menu
		&::before {
			top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

		//third line menu
		&::after {
			top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

	// hover
	.no-touchevents &:hover {
		//background-color: #f3f3f3

		&::before, &::after {
			background-color: $base-color; }

		& > i {
			background-color: $base-color;

			&::before, &::after {
				background-color: $base-color; } } }

	// active
	&.nav-is-open {
		background-color: #fff;

		&::before, &::after {
			transition-delay: $btn-menu-animation-delay;
			//background-color: $base-color

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } } }

// overlay
// в html добавить
// <div class="nav-overlay nav-overlay-js">&nbsp;</div>
.overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(black, 0.35);
	z-index: 9998;
	opacity: 0;
	visibility: hidden;
	$prop: opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	&.nav-overlay {
		top: $header-height--sm; }

	&.open-only-mob {
		@include media('>=tab-lg') {
			display: none; } }

	&.#{$nav-is-open-class} {
		opacity: 1;
		visibility: visible; }

	&.filters-is-open, &.request-is-open {
		opacity: 1;
		visibility: visible; } }

/* navigation end */

