/* === FOOTER === */
.footer {
	//margin: 0 auto
	position: relative;
	clear: both;
	z-index: 5;
	@include font(16px, round-float(24/16, 3));
	color: $text-color;
	border-top: 1px solid $border-color;

	background-color: lightgreen; }

.footer__holder {
	overflow: hidden;
	cursor: default;
	padding: $footer-grid-space; }

/* footer bottom */
.footer-bottom {
	position: relative;
	// обрезать вытянутый маржинами по горизонтале дочерний элемент
	overflow: hidden; }

.footer-bottom__holder {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	@include margin-horizontal(-#{round(nth($grid-space, 2)/2)});
	margin-bottom: -#{round(nth($grid-space, 1)/2)}; }

.footer-bottom__col {
	text-align: center;
	@include padding-horizontal(round(nth($grid-space, 2)/2));
	margin-bottom: #{round(nth($grid-space, 1)/2)};
	// для браузеров не поддерживающих display: flex
	display: inline-block;
	vertical-align: top;
	width: calc(33.33% - 5px);

	@include media('<mob-lg') {
		min-width: 100%; }

	&:first-child {
		@include media('>=mob-lg') {
			text-align: left; } }

	&:last-child {
		@include media('>=mob-lg') {
			text-align: right; } }

	a {
		color: inherit;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	@include media('<mob-lg') {
		display: block; }

	i {
		@include media('<mob-lg') {
			display: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px); }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover
	//ransform: translate(0, 1px)
	//ilter: blur(2px)
	//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

