@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.user-form {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	.form-title {
		display: block;
		@include font-alt-bold;
		color: $title-color;
		text-align: center;
		margin-bottom: $bottom-space;

		// font size
		& {
			@include font-h2; } }

	form {
		display: block; }

	.input-wrap {
		&:last-child {
			margin-bottom: 0; } }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		[class*="btn-"] {
			@include media('<mob-lg') {
				display: block;
				width: 100%;
				margin-top: $bottom-space--small;

				&:first-child {
					margin-top: 0; } } } }

	.form-note {
		overflow: hidden;
		min-height: $btn-height;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--small, $line-height--small);
		letter-spacing: $letter-spacing; } }

/* user form end */

/* swiper slider */
$swiper-button-size: 52px, 22px;
$swiper-button-size--md: 40px, 16px;
$swiper-arrow-weight: 2px; // толщина "древка" и линий "острея"
$swiper-arrow-width: nth($swiper-button-size, 1) - (round($swiper-arrow-weight/2)); // длинна "древка" стрелки должно быть меньше длинны кнопки на несколько пискелов, чтобы не перекрывать "острие"
$swiper-arrow-width--md: nth($swiper-button-size--md, 1) - (round($swiper-arrow-weight/2));
$swiper-arrow-size: 14px; // размер линий "острея"
$swiper-arrow-size--md: 10px; // размер линий "острея"

.swiper-button-prev, .swiper-button-next, .def-arrows--prev, .def-arrows--next {
	overflow: hidden;
	text-indent: -9999px;
	@include size(nth($swiper-button-size, 1), nth($swiper-button-size, 2));
	margin-top: -#{round(nth($swiper-button-size, 2)/2)};
	will-change: transform;
	//background: rgba(255, 127, 80, 0.51) // для тестирования, чтобы видеть контуры кнопки
	background: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	@include media('<tab-lg') {
		@include size(nth($swiper-button-size--md, 1), nth($swiper-button-size--md, 2));
		margin-top: -#{round(nth($swiper-button-size--md, 2)/2)}; }

	&::before, &::after {
		@include pseudo();
		border: $swiper-arrow-weight solid $text-color;
		transform: none; }

	&::before {
		@include position(50%);
		@include size($swiper-arrow-width, 0);
		border-left: none;
		border-right: none;
		border-bottom: none;
		margin: -#{round($swiper-arrow-weight/2)} 0 0 -#{round($swiper-arrow-width/2)};

		@include media('<tab-lg') {
			@include size($swiper-arrow-width--md, 0);
			margin: -#{round($swiper-arrow-weight/2)} 0 0 -#{round($swiper-arrow-width--md/2)}; } }

	&::after {
		@include size($swiper-arrow-size);
		border-bottom: none;
		top: 50%;

		@include media('<tab-lg') {
			@include size($swiper-arrow-size--md); } } }

// hover & disabled
.swiper-button-prev, .swiper-button-next {

	// hover
	&:not(.swiper-button-disabled):hover {
		&::before, &::after {
			border-color: $base-color; } }

	// disabled
	&.swiper-button-disabled {
		pointer-events: auto; } }

.def-arrows--prev, .def-arrows--next {
	position: relative;

	// hover
	&:not(.def-arrows--disabled):hover {
		&::before, &::after {
			border-color: $base-color; } }

	// disabled
	&.def-arrows--disabled {
		pointer-events: auto; } }

// prev arrow
.swiper-button-prev, .def-arrows--prev {
	&::after {
		left: 0;
		border-right: none;
		transform-origin: left top;
		transform: rotate(-45deg); } }

// next arrow
.swiper-button-next, .def-arrows--next {
	&::after {
		right: 0;
		border-left: none;
		transform: rotate(45deg);
		transform-origin: right top; } }

.swiper-button-prev.swiper-button-disabled {
	pointer-events: auto;
	cursor: default; }

/* swiper slider end */

